(function ($) {
    $.fn.schoolCalendar = function (options) {
        var opts = $.extend( {}, $.fn.schoolCalendar.defaults, options );
        var $container = opts.container || $(this);
        $container.addClass('calendar-container');
        var ADAY = 86400000;

        var startDate = opts.startDate;
        // console.log(startDate);
        var endDate = opts.endDate;
        // console.log(endDate);


        var startWeekday = startDate.getDay();
        // console.log(startWeekday);
        var weekList = [];
        var currentDate = new Date(startDate.getTime());
        var currentWeek = 0;
        weekList[currentWeek] = Array.apply(null, Array((startWeekday - 1 + 7) % 7)).map(function () {
            return null
        });
        var currentWeekday = (startWeekday - 1) % 7;

        while(currentDate.getTime() <= endDate.getTime()) {
            if (!weekList[currentWeek]) weekList[currentWeek] = [];
            weekList[currentWeek][currentWeekday++] = currentDate;
            currentDate = new Date(currentDate.getTime() + ADAY);
            if (currentWeekday == 7) {
                currentWeek++;
                currentWeekday = 0;
            }
        }
        var lastIndex = weekList.length - 1;
        var lastWeek = weekList[lastIndex];
        weekList[lastIndex] = lastWeek.concat(Array.apply(null, Array(7 - lastWeek.length)));
        // console.log(weekList);

        var $table = $('<table><thead><tr><th>月份</th><th>周数</th><th>一</th><th>二</th><th>三</th><th>四</th><th>五</th><th>六</th><th>日</th></thead><tbody></tbody></table>');
        $table.appendTo($container);
        var $tbody = $table.find('tbody');
        var previousMonth = -1;
        var rowspan = 0;
        var stripe = false;
        weekList.forEach(function (w, i) {
            var $tr = $('<tr></tr>');
            var currentMonth = w.filter(function (d) {return d})[0].getMonth();
            // week number
            var $weektd = $('<td></td>').text(i + 1);
            if (stripe) $weektd.addClass('td-stripe');
            $tr.append($weektd);
            // week days
            w.forEach(function (d) {
                if (d) {
                    var $td = $('<td></td>');
                    $td.text(d.getDate()).addClass('td-date').data('date', d);
                    $tr.append($td);
                    if (d.getDate() === 1) {
                        stripe = !stripe;
                    }
                    if (stripe) {
                        $td.addClass('td-stripe');
                        var $tpr = $td.prev('td');
                        if ($tpr && !$tpr.hasClass('td-date')) {
                            $tpr.addClass('td-stripe');
                        }
                    }
                } else {
                    $tr.append($('<td></td>').addClass('empty'));
                }
            });
            $tbody.append($tr);
            rowspan++;
            // month
            if (currentMonth !== previousMonth) {
                var $monthtr = $tbody.find('.tr-previous');
                var $monthtd = $monthtr.find('td').eq(0)
                $monthtd.attr('rowspan', rowspan)
                if (!stripe) {
                    $monthtd.addClass('td-stripe');
                    for (var i = 0; i < rowspan; i++) {
                        $monthtr
                    }
                }
                $monthtr.removeClass('tr-previous');
                rowspan = 0;
                $tr.addClass('tr-month tr-previous').prepend($('<td></td>').text(currentMonth + 1 + '月'));
                previousMonth = currentMonth;
            }
        });
        rowspan++;
        $tbody.find('.tr-previous').find('td').eq(0).attr('rowspan', rowspan).end().end().removeClass('tr-previous');


        var selectOn = false;
        $tbody.on('mousedown', function (e) {
            var $t = $(e.target);
            if ($t.hasClass('td-date')) {
                selectOn = true;
                $t.toggleClass('active');
                getSelect();
            }
        }).on('mouseup', function (e) {
            var $t = $(e.target);
            selectOn = false;
        }).on('mouseout', function (e) {
            selectOn = false;
        }).on('mouseover', function (e) {
            var $t = $(e.target);
            if (selectOn && $t.hasClass('td-date')) {
                $t.addClass('active');
                getSelect();
            }
            e.stopPropagation();
        })
        .end().find('.td-date').on('mousein', function (e) {
            e.stopPropagation();
        }).on('mouseout', function (e) {
            e.stopPropagation();
        });

        $('body').on('mouseover', function () {
            selectOn = false;
        })

        function getSelect() {
            var arr = [];
            $tbody.find('.td-date.active').each(function (i, td) {
                arr.push($(td).data('date'));
            });
            opts.onChange.call(null, arr);
        }
        this.clear = function () {
            $tbody.find('.td-date').removeClass('active');
            getSelect();
        };
        return this;
    };
    $.fn.schoolCalendar.defaults = {
        startDate: new Date('2017-02-20'),
        endDate: new Date('2017-07-02'),
        onChange: function (dates) {
            // console.log(dates);
        }
    }
}(jQuery));
